export let nodeMixin = {
    methods: {
        /**
         * slider<=100
         * @param {int} val 
         * @param {[]} data 
         */
        propChange(val, data) {
            let already = 0;
            data.forEach((item) => {
                if (item.name != val.name) {
                    already += item.prop;
                }
            });
            val.prop = val.prop < 100 - already ? val.prop : 100 - already;
        },
    },

    watch: {
        /**
     * mutations
     */
        nodeData: {
            handler(val) {
                this.STORAGE_GRAPH();
            },
            deep: true,
        },
    },

}